<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Loading...</span>
        </div>
    </div>
   <DeptoBase
    :goal="choferes.meta_semana"
    :progress="choferes.progress"
    :name="choferes.nombre"
    :depto="choferes.depto"
    :days="choferes.dias_laborados"
    :m3="choferes.m3_persona"
    :bonus_p="choferes.bono_persona"
    :bonus_tot="choferes.bono_depto"
    :asis="choferes.asistencia"
    :extra="choferes.$_extra_m3"
    :bonus_prod="choferes.bono_productividad"
    :bonus_goals="choferes.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Choferes',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'choferes',
            userCode: this.$route.params.user,
            city: 'juarez'
        });  
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        choferes() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>